import { Action, createAction, props } from '@ngrx/store';
import { QuantityModel } from '../../views/theme/productinfo/model/quantity.model';
import { Update } from '@ngrx/entity'; //For Update 'Entity used'

//List Data
export const loadQuantities = createAction(
  '[Load Quantities] List Quantities',
);

export const loadQuantitiesSuccess = createAction(
  '[Load Quantities] List Quantities Success',
  props<{quantity: any}>()
);

export const loadQuantitiesFail = createAction(
  '[Load Quantities] List Quantities Fail',
  props<{error: any}>()
)


//Load Single data
export const loadQuantity = createAction(
  '[Load Quantity] Single Quantity',
  props<{pid: any}>()
);

//Load update data 
export const updateQuantity = createAction(
  '[Quantity] Update Quantity',
  props<{ quantity: any; pid: any }>()
);

export const updateQuantitySuccess = createAction(
  '[Quantity] Update Quantity Success',
  props<{ quantity: Update<QuantityModel> }>() 
  //'Update'Taken from '@ngrx/entity' file and 'QuantityModel' taken from model
);

export const updateQuantityFail = createAction(
  '[Quantity] Update Quantity Fail',
  props<{ error: any }>()
);